import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';


export interface IBusinessRoleAppRole {
  id?: string;
  application: string;
  applicationRole: string;
}

@Component({
  selector: 'app-br-application-role-list',
  templateUrl: './br-application-role-list.component.html',
  styleUrls: ['./br-application-role-list.component.scss']
})
export class BrApplicationRoleListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  brRoles: IBusinessRoleAppRole[] = [
    {
      id: '1',
      application: 'SVC',
      applicationRole: 'Manager',
    },
    {
      id: '2',
      application: 'BOM',
      applicationRole: 'Frontline',
    }
  ];

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      application: ['', [Validators.required]],
      applicationRole: ['', [Validators.required]],
    });
  }

  showAppForm(): void {
    this.modalTitle = 'New Application Role';
    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const { application, applicationRole }: IBusinessRoleAppRole = this.form.value;

    this.brRoles.push({
      id: new Date().getTime().toString(),
      application,
      applicationRole
    });

    this.form.reset();
  }

  deleteApp(app: IBusinessRoleAppRole): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.brRoles = this.brRoles.filter( x => x.id !== app.id);
    }
  }

}
