import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';


export interface IBusinessRoleFunctionGroup {
  id?: string;
  application: string;
  functionGroup: string;
}

@Component({
  selector: 'app-br-function-group-list',
  templateUrl: './br-function-group-list.component.html',
  styleUrls: ['./br-function-group-list.component.scss']
})
export class BrFunctionGroupListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  brFunctionGroups: IBusinessRoleFunctionGroup[] = [
    {
      id: '1',
      application: 'SVC',
      functionGroup: 'IBS Frontline',
    },
    {
      id: '2',
      application: 'BOM',
      functionGroup: 'PTC Manager',
    }
  ];

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      application: ['', [Validators.required]],
      functionGroup: ['', [Validators.required]],
    });
  }

  showAppForm(): void {
    this.modalTitle = 'New Function Group';
    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const { application, functionGroup }: IBusinessRoleFunctionGroup = this.form.value;

    this.brFunctionGroups.push({
      id: new Date().getTime().toString(),
      application,
      functionGroup
    });

    this.form.reset();
  }

  deleteApp(app: IBusinessRoleFunctionGroup): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.brFunctionGroups = this.brFunctionGroups.filter( x => x.id !== app.id);
    }
  }

}
