import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { BusinessRolesComponent } from './business-roles/business-roles.component';
import { BusinessRoleViewComponent } from './business-role-view/business-role-view.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { ReactiveFormsModule } from '@angular/forms';
import { BrApplicationRoleListComponent } from './br-application-role-list/br-application-role-list.component';
import { BrFunctionGroupListComponent } from './br-function-group-list/br-function-group-list.component';

const routes: Routes = [
  { path: '', component: BusinessRolesComponent },
  { path: ':id', component: BusinessRoleViewComponent }
];


@NgModule({
  declarations: [
    BusinessRolesComponent,
    BusinessRoleViewComponent,
    BrApplicationRoleListComponent,
    BrFunctionGroupListComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes)
  ]
})
export class BusinessRoleModule { }
