import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

export interface IBusinessRole {
  id?: string;
  name: string;
  description: string;
}

@Component({
  selector: 'app-business-roles',
  templateUrl: './business-roles.component.html',
  styleUrls: ['./business-roles.component.scss']
})
export class BusinessRolesComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  businessRoles: IBusinessRole[] = [
    {
      id: '1',
      name: 'Shop Sales',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    },
    {
      id: '2',
      name: 'Shop Manager',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    }
  ];

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
      description: '',
    });
  }

  showAppForm(type: string, role?: IBusinessRole): void {
    if (type === 'add') {
      this.modalTitle = 'New Business Role';
    } else {
      this.modalTitle = `Edit ${role.name}`;
      this.form.setValue(role);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name, description }: IBusinessRole = this.form.value;

    if (id) {
      this.businessRoles.map( x => {
        if (id === x.id) {
          x.name = name;
          x.description = description;
        }
      });
    } else {
      this.businessRoles.push({
        id: new Date().getTime().toString(),
        name, description
      });
    }

    this.form.reset();
  }

  deleteApp(br: IBusinessRole): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.businessRoles = this.businessRoles.filter( x => x.id !== br.id);
    }
  }

}
